<template>  
<div class="container">
    <div class="login">
       <p style="position:absolute;top:35%;left:41%;color: #ececec;">系统登录</p>
        <el-form :model="loginForm" ref="loginForm" status-icon :rules="loginRules" class="demo-ruleForm" method="post" 
        action="http://localhost:9091/user/login" style="width: 60%;margin-top:12%;">
            <el-form-item prop="username">
                <el-input v-model="loginForm.username"></el-input>
            </el-form-item>
            <el-form-item style="margin:0;" prop="password">
                <el-input  type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item style="display:block;float;left;clear:both;height:15px;">
                <el-checkbox v-model="loginForm.isLogin">记住我</el-checkbox> 
            </el-form-item>
                <a href="#"><span>忘记密码?</span></a>
                <a href="/register"><span style="float: right;">注册</span></a>     
            <el-button type="primary" style="width: 100%;" @click="login">登录</el-button>
        </el-form>
    </div>
</div>
</template>
<script>
    export default({
        data() {
            return {
                loginForm: {
                    username:'',
                    password:'',
                    isLogin: 'false'
                },
                loginRules:{
                    username: [
                        { required:  true, message: '请输入用户名',trigger: 'blur' },
                        { min: 3, max: 7, message: '用户名长度3-7个字符', trigger: 'blur' }
                    ],
                    password: [
                        { required:  true, message: '请输入密码',trigger: 'blur' },
                        { min: 3, max: 20, message: '密码长度3-20个字符', trigger: 'blur' },
                    ]
                },
            }
        },
        methods: {
            login: function(event) {
                var params = new URLSearchParams();
                params.append('username',this.loginForm.username)
                params.append('password',this.loginForm.password)
                params.append('isLogin',this.loginForm.isLogin)
                this.$http.post('http://localhost:9091/user/login',params).then((res)=> { 
                    if(res.data.code == 200){
                        var loginId = this.$cookie.get("loginId")
                        var username = this.$cookie.get("username")
                        var token = this.$cookie.get("token")
                        if((loginId != res.data.data.loginId || loginId == null) && 
                           (username != res.data.data.username || username == null) &&
                           (token != res.data.data.token || token == null))

                        this.$cookie.set("loginId",res.data.data.loginId)
                        this.$cookie.set("username",res.data.data.username)
                        this.$cookie.set("token",res.data.data.token)
                        if(this.loginForm.isLogin){
                            this.$cookie.set("token",res.data.data.token, {expires: '10h'})
                            console.log(res.data.data)
                        }
                        this.$message.success("登录成功");
                        this.$router.push('/');

                    } else {
                        this.$message.error("用户名或密码错误");
                        console.log(res.data.code)
                    }
                    

                    /*if(token == null){
                        this.$cookie.remove("token")
                        this.$cookie.remove("user")
                        this.$cookie.remove("loginId")
                        this.$cookie.remove("username")
                        this.$router.push('/login');
                    }*/
                })
            }
        }
    })
</script>



