<template>
  <el-row style="width:100%;height:100%;overflow:hidden;">
    <el-col style="width:100%;height:100%;">
      <el-container>
        <el-header :span="24" style="height:50px;">
        	<span>RynOa</span>	
          <el-col style="width:130px;height:100%;float:right;">
            <el-dropdown trigger="click" style="display:flex;align-items:center;justify-content:center;top:30%;">
              <span class="el-dropdown-link" style="color:white;font-size:13px;">
                <span>{{this.$cookie.get("username")}}</span>
                <i class="el-icon-arrow-down"></i>
              </span>
              <el-dropdown-menu slot="dropdown" style="width:130px;height:80px;margin-top:1.1rem">
                <el-dropdown-item>账户<i class="fa fa-user fa-fw"></i><hr></el-dropdown-item>
                <el-dropdown-item>登出<i class="fa fa-sign-out fa-fw"></i></el-dropdown-item>
              </el-dropdown-menu>  
            </el-dropdown>
          </el-col>
          <span style="width:80px;height:50px;;font-size:10px;float:right;">系统设置</span>
          <i class="fa fa-wrench fa-fw" style="width:35px;height:50px;float:right;display:flex;align-items:center;justify-content:center;font-size:16px;"></i>
        </el-header>
      </el-container>
         <el-container style="width:100%;height:100%;">	
          <el-aside style="width:12%;height:100%;">
            <el-menu class="el-menu-vertical-demo" background-color="#3d464d" text-color="rgba(255, 255, 255, 0.6)"  active-text-color="rgba(255, 255, 255, 0.6)"
            :collapse="isCollapse">
            <p style="text-align:right;margin-right:5%;color:#9B9999;margin-top:5%;margin-bottom:0;">
            	<i class="fa fa-bars fa-fw" v-on:click="Collapse"></i></p>	
              <el-menu-item index="1" style="margin-top:10px;"><i class="fa fa-dashboard fa-fw"></i><span slot="title">控制台</span></el-menu-item>
                <el-submenu index="2">
                <template slot="title">
                  <i class="fa fa-folder fa-fw"></i><span>项目管理</span>
                </template>
                  <el-menu-item-group>
                    <el-menu-item index="2-1"><i class="fa fa-list-ul fa-fw"></i><span>项目列表</span></el-menu-item>
                    <el-menu-item index="2-2"><i class="fa fa-tasks fa-fw"></i><span>任务列表</span></el-menu-item>
                    <el-menu-item index="2-3"><i class="fa fa-flag fa-fw"></i><span>项目动态</span></el-menu-item>
                    <el-menu-item index="2-4"><i class="fa fa-book fa-fw"></i><span>项目文档</span></el-menu-item>
                    <el-menu-item index="2-5"><i class="fa fa-pencil fa-fw"></i><span>项目计划</span></el-menu-item>
                  </el-menu-item-group>
                </el-submenu>            		
              	<el-submenu index="3">
                <template slot="title">
                  <i class="fa fa-user-circle fa-fw"></i><span>审批管理</span>
                </template>
                  <el-menu-item-group>
                  	<el-menu-item index="1-1"><i class="fa fa-list-ul fa-fw"></i><span>审批列表</span></el-menu-item>
                    <el-menu-item index="1-2"><i class="fa fa-folder fa-fw"></i><span>项目审批</span></el-menu-item>
                    <el-menu-item index="1-3"><i class="fa fa-dollar fa-fw"></i><span>经费审批</span></el-menu-item>
                    <el-menu-item index="1-4"><i class="fa fa-calendar fa-fw"></i><span>考勤审批</span></el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
                <el-submenu index="4">
                <template slot="title">
                  <i class="fa fa-sitemap fa-fw"></i><span>权限管理</span>
                </template>
                  <el-menu-item-group>
                    <el-menu-item index="1-1">选项1</el-menu-item>
                    <el-menu-item index="1-2">选项2</el-menu-item>
                    <el-menu-item index="1-3">选项2</el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
                <el-submenu index="5">
                <template slot="title">
                  <i class="fa fa-address-book fa-fw"></i><span>联系人管理</span>
                </template>
                  <el-menu-item-group>
                    <el-menu-item index="1-1">选项1</el-menu-item>
                    <el-menu-item index="1-2">选项2</el-menu-item>
                    <el-menu-item index="1-3">选项2</el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
                <el-menu-item index="6"><i class="fa fa-bullhorn fa-fw"></i><span slot="title">公告栏</span></el-menu-item>
                <el-menu-item index="7"><i class="fa fa-bar-chart fa-fw"></i><span slot="title">统计</span></el-menu-item>
              </el-menu>
          </el-aside>
          <el-main style="width:75%;height:100%;padding:0px;">
            <el-container style="width:100%;height:100%;float:left;margin:0px;padding:0px;display:block;">
              <header>
                <el-breadcrumb separator="/" style="margin-left:1rem;line-height:35px;">
                  <el-breadcrumb-item :to="{ path: '/index' }">首页</el-breadcrumb-item>
                  <el-breadcrumb-item>控制台</el-breadcrumb-item>
                </el-breadcrumb>
              </header>
              <el-main style="width:100%;height:100%;float:left;">
              	<div class="card red">
              		<div class="card-body">
              			<i class="fa-comments fa fa-4x"></i>
              			<div class="card-content">
              				<div class="card-title">20</div>
              				<div class="card-sub-title">NewMessage</div>
              			</div>
              			<div class="clear-both"></div>
              		</div>	
              	</div>
              	<div class="card yellow">
              		<div class="card-body">
              			<i class="fa-bell fa fa-4x"></i>
              			<div class="card-content">
              				<div class="card-title">50</div>
              				<div class="card-sub-title">NewNotification</div>
              			</div>
              		</div>	
              	</div>
              	<div class="card green">
              		<div class="card-body">
              			<i class="fa-flag fa fa-4x"></i>
              			<div class="card-content">
              				<div class="card-title">30</div>
              				<div class="card-sub-title">NewTrends</div>
              			</div>
              		</div>	
              	</div>
              	<div class="card blue">
              		<div class="card-body">
              			<i class="fa-user fa fa-4x"></i>
              			<div class="card-content">
              				<div class="card-title">70</div>
              				<div class="card-sub-title">NewUser</div>
              			</div>
              		</div>	
              	</div>
              </el-main>
            </el-container>
          </el-main>
      	</el-container>
    </el-col>
  </el-row>
</template>

<script>
export default {
    data() {
      return {
        isCollapse: false,
        counter: 0
      }
    },
    methods: {
    Collapse: function() {
    	this.$data.counter +=1
    	if(this.$data.counter %2 !==0){
    		this.$set(this.$data,'isCollapse',true);
    		console.log(this.$data.counter);
    	}else{
    		this.$set(this.$data,'isCollapse',false);
    	}
    }
      /*	if(index === 1){
      		this.$set(this.$data,'isCollapse',true);	
      	}else{

      		this.$set(this.$data,'isCollapse',false);
      	}*/
    }
  }
</script>


