<template>
<div class="container">
    <div class="reg">
        <p style="margin-top: -8%">用户注册</p>
        <el-form :model="regForm" ref="regForm"  :rules="regRules" class="demo-ruleForm">
            <el-form-item label="用户名:" prop="username" :error="regForm.errors.username">
                <el-input v-model="regForm.username" @input="checkUser"></el-input>
            </el-form-item>
            <el-form-item label="密码:" prop="pass">
                <el-input type="password" v-model="regForm.pass" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="确认密码:" prop="checkPass">
                <el-input type="password" v-model="regForm.checkPass" auto-complete="off"></el-input>
            </el-form-item>
                <el-form-item label="登录角色:" prop="loginRole">
                 <el-radio v-model="regForm.loginRole" label="user">用户</el-radio>
                <el-radio  v-model="regForm.loginRole" label="admin">管理员</el-radio>
            </el-form-item>
            <el-form-item label="邮箱地址:" prop="email" :error="regForm.errors.email">
                <el-input v-model="regForm.email"  @input="checkEmail"></el-input>
            </el-form-item>
            <el-form-item style="display: flex;justify-content: center">
                <el-button type="primary" v-on:click="submitForm('regForm')">注册</el-button>
                 <el-button @click="resetForm('regForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</div>    
</template>
<script>
 export default({
        data(){
            var validatePass = (rule,value,callback) => {
                if (this.regForm.checkPass !== '') {
                    this.$refs.regForm.validateField('checkPass');
                }
                callback();
            };
            var validateCheckPass = (rule,value,callback)=> {
                if (value !== this.regForm.pass){
                    callback(new Error('两次输入的密码不一致'));
                }
                    callback();
            };
            return {
                regForm: {
                    username: '',
                    pass: '',
                    checkPass: '',
                    loginRole: 'user',
                    email: '',
                    errors: {
                        username: '',
                        email: '',
                    }
                },
                regRules: {
                    username: [
                        {required: true, message: '请输入用户名', trigger: 'blur'},
                        {min: 3, max: 7, message: '用户名长度3-7个字符', trigger: 'blur'}
                    ],
                    pass: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                        {min: 3, max: 20, message: '密码长度3-20个字符', trigger: 'blur'},
                        {validator: validatePass, trigger: 'blur'}
                    ],
                    checkPass: [
                        {required: true, message: '请再次输入密码', trigger: 'blur'},
                        {min: 3, max: 20, message: '密码长度3-20个字符', trigger: 'blur'},
                        {validator: validateCheckPass, trigger: 'blur'}
                    ],
                    email: [
                        {message: '请输入邮箱地址', trigger: 'blur'},
                        {type: 'email', message: '请输入正确邮箱地址', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
           checkUser: function(event){
                var username = JSON.parse(window.localStorage.getItem("username"))
                if(this.regForm.username == username){
                   this.regForm.errors = {username: '用户名已存在'}
                }
            },
            checkEmail: function(event){
                 var email = JSON.parse(window.localStorage.getItem("email"))
                if(this.regForm.email == email){
                   this.regForm.errors = {email: '邮箱地址已存在'}
                }
            },
            submitForm(formName){
                this.$refs[formName].validate((valid) => {
                  if(valid) {
                    var params = new URLSearchParams()
                    params.append('username',this.regForm.username)
                    params.append('password',this.regForm.pass)
                    params.append('loginRole',this.regForm.loginRole)
                    params.append('email',this.regForm.email)

                    this.$http.post('http://localhost:9091/user/register',params).then((res)=>{
                        if(!res.data.userError && !res.data.emailError){
                            var name = window.localStorage.getItem("username",JSON.stringify(res.data.username))
                            var email = window.localStorage.getItem("email",JSON.stringify(res.data.email))
                            if((name !== res.data.username || name == null) && (email !== res.data.email || email == null)){
                              window.localStorage.setItem("username",JSON.stringify(res.data.username))
                              window.localStorage.setItem("email",JSON.stringify(res.data.email))                          
                            }
                        this.$message.success("注册成功")
                        this.$router.push('/login')
                        } else {
                        this.$message.error("注册失败")   
                        }    
                    })

                  } else{
                      this.$message.error("注册失败")
                      return false;
                  }
                });
            },
            resetForm(formName) {
                this.$nextTick(function () {
                    this.$refs[formName].resetFields();
                })
            }
        }
    })
</script>


